body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(to right bottom, #37051f, #540a43, #661e73, #633bac, #125eeb);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1{
  color: white;
  text-align: center;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-size: 200%;
  text-align: center;
  font-weight: 700;
}

h2{
  color: white;
  text-align: center;
  padding: 10px;
}

h3{
  color: black;
}

.abs-center {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 15vh;
}

.form {
  width: 450px;
}

.Lista{
  background-color: white;
  border: 10px;
  padding: 10px;
  padding-left: 10px;
  padding-right: 10px;
  border-radius: 5px;
}